export default [
  {
    path: '/shop',
    name: 'shop',
    component: () => import('@/views/shop/shop/Shop.vue'),
    meta: {
      resource: 'Auth',
      action: 'read',
    },
  },
  {
    path: '/shop/product/collection/all',
    name: 'product-collection',
    component: () => import('@/views/shop/e-commerce-shop/ECommerceShop.vue'),
    meta: {
      resource: 'Auth',
      action: 'read',
    },
  },
  {
    path: '/shop/product/:slug',
    name: 'shop-product-details',
    component: () => import('@/views/shop/e-commerce-product-details/ECommerceProductDetails.vue'),
    meta: {
      resource: 'Auth',
      action: 'read',
    },
  },
  {
    path: '/shop/checkout',
    name: 'shop-checkout',
    component: () => import('@/views/shop/e-commerce-checkout/ECommerceCheckout.vue'),
    meta: {
      resource: 'Auth',
      action: 'read',
    },
  },
  {
    path: '/shop/pool/collection/all',
    name: 'pool-collection',
    component: () => import('@/views/shop/e-commerce-shop/ECommerceShop.vue'),
    meta: {
      resource: 'Auth',
      action: 'read',
    },
  },
]
